import { FunctionComponent } from "react";
import {
    Card,
    CardContent,
    CardActions,
    Typography,
    Button,
    Icon,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 700,
    },
    content: {},
    title: {
        fontWeight: "bold",
        fontSize: 20,
    },
    description: {
        marginTop: theme.spacing(2),
        fontWeight: 400,
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        width: "100%",
    },
    action: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
    },
    actionIcon: {
        marginRight: theme.spacing(1),
        // fontSize: 16
    },
}));

export interface Props {
    title?: string;
    description?: string;
    onEdit: () => void;
}

const ViewCourse: FunctionComponent<Props> = (props: Props) => {
    const classes = useStyles();
    const { title, description, onEdit } = props;
    return (
        <Card className={classes.root}>
            <CardContent className={classes.content}>
                <Typography className={classes.title}>
                    {title || "Please specify a title"}
                </Typography>
                <Typography className={classes.description}>
                    {description || "Please specify a description"}
                </Typography>
            </CardContent>
            <CardActions className={classes.actions}>
                <Button className={classes.action} onClick={onEdit}>
                    <Icon className={classes.actionIcon} fontSize="small">
                        edit
                    </Icon>
                    Edit
                </Button>
            </CardActions>
        </Card>
    );
};

export default ViewCourse;
