import { FunctionComponent, useState, useCallback, useEffect } from "react";
import {
    Icon,
    Button,
    Toolbar,
    CircularProgress,
    Breadcrumbs,
    Link,
} from "@material-ui/core";
import { Pagination } from "@material-ui/lab";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import { gql, useQuery } from "@apollo/client";

import { usePagination } from "../../hooks";
import { useAppSelector } from "../../redux/hooks";
import { Article } from "../../types";
import ViewArticle from "./ViewArticle";
import NoRecords from "../common/NoRecords";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        minHeight: "100vh",
        backgroundColor: theme.palette.background.default,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    toolbar: {
        backgroundColor: (theme.palette.background as any).level2,
        width: "100%",
    },
    toolbarContainer: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
        width: "100%",
    },
    link: {
        cursor: "pointer",
    },
    title: {
        color: "white",
        fontWeight: "bold",
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    articles: {
        width: "100%",
        padding: theme.spacing(4),
    },
    progress: {
        width: 40,
        height: 40,
    },
    pagination: {
        marginTop: theme.spacing(2),
    },
    noRecords: {
        width: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "calc(100vh - 128px)",
    },
}));

const GET_ARTICLES = gql`
    query GetArticles($page: Int!, $limit: Int!) {
        getArticles(page: $page, limit: $limit) {
            totalPages
            records {
                id
                title
                description
            }
        }
    }
`;

const ViewArticles: FunctionComponent = () => {
    const classes = useStyles();
    const history = useHistory();
    const { generateURL, page, setPage, rowsPerPage } = usePagination();
    const { loading, error, data, refetch } = useQuery(GET_ARTICLES, {
        variables: {
            page,
            limit: rowsPerPage,
        },
    });
    const { firstName } = useAppSelector((state) => state.session.payload);

    const handlePageChange = useCallback(
        (event, newPage) => {
            setPage(newPage - 1);
            generateURL(newPage - 1, rowsPerPage);
            window.scrollTo(0, 0);

            refetch({
                page: newPage - 1,
                limit: rowsPerPage,
            });
        },
        [generateURL, refetch, rowsPerPage, setPage],
    );

    const handleEditArticle = useCallback(
        (id) => () => {
            history.push(`/articles/${id}/edit`);
        },
        [history],
    );

    const handleNewArticle = useCallback(() => {
        history.push(`/articles/new`);
    }, [history]);

    const handleOpenArticles = useCallback(() => {
        history.push("/articles");
    }, [history]);

    if (error) {
        throw error;
    }

    const { records = [], totalPages = 0 } = data?.getArticles || {};

    const renderArticles = () =>
        records.map((article: Article) => (
            <div className={classes.articles}>
                <ViewArticle
                    title={article.title}
                    description={article.description}
                    onEdit={handleEditArticle(article.id)}
                />
            </div>
        ));

    return (
        <div className={classes.root}>
            <Toolbar className={classes.toolbar}>
                <div className={classes.toolbarContainer}>
                    <Breadcrumbs separator="â€º">
                        <Link
                            color="inherit"
                            onClick={handleOpenArticles}
                            className={classes.link}>
                            Articles
                        </Link>
                    </Breadcrumbs>
                    <div className={classes.actions}>
                        <Button onClick={handleNewArticle}>
                            <Icon className={classes.actionIcon}>
                                add_circle
                            </Icon>{" "}
                            New Article
                        </Button>
                    </div>
                </div>
            </Toolbar>
            {loading && <CircularProgress className={classes.progress} />}
            {!loading && records.length > 0 && renderArticles()}
            {!loading && records.length > 0 && totalPages > 1 && (
                <Pagination
                    className={classes.pagination}
                    color="secondary"
                    count={totalPages}
                    page={page + 1}
                    onChange={handlePageChange}
                />
            )}
            {!loading && records.length === 0 && (
                <div className={classes.noRecords}>
                    <NoRecords
                        message={`Are you ready to create your first article, ${firstName}?`}
                        actionText="Create New Article"
                        actionHandler={handleNewArticle}
                        image={`${process.env.PUBLIC_URL}/assets/images/empty-articles.svg`}
                        action={true}
                    />
                </div>
            )}
        </div>
    );
};

export default ViewArticles;
